@import '../includes/variables';
@import '../includes/mixins';

.block-banner {
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;

    .wrapper {
        padding: 200px 0;
        height: 100%
    }

    .title {
        font-size: 105px;
        line-height: 115px;
        color: $white;
    }

    .list-sub-title {
        pointer-events: none;

        .title {
            //text-shadow: 5px 5px 5px transparent;
            -webkit-text-fill-color: transparent;
            -webkit-text-stroke: 1px $white;
        }
    }

    .link {
        color: $white;
        width: 411px;
        padding-bottom: 20px;
        border-bottom: 1px solid $white;
        position: absolute;
        bottom: 70px;
        left: 0;

        i {
            position: absolute;
            right: 0;
            top: 7px;
        }
    }

    .logo-banner {
        position: absolute;
        bottom: -100px;
        right: 30%;
    }

    .icon-next-sec {
        position: absolute;
        bottom: 70px;
        right: 0;
    }
}